<template>
	<div>
		<div class="app-header">
			<div class="app-header-inner">
<!-- 				套一个router-link，点击logo的时候就会跳转到首页 -->
				<router-link :to="{path : '/'}">
				<img src="../assets/logo.png" alt="头部logo">
				</router-link>
				<div class="header-nav">
					<ul class="nav-list">
						<li>{{ username }}</li>
						<li v-if="username === ''" @click="logClick">登录</li>
						<li v-if="username === ''" class="nav-pile">|</li>
						<li v-if="username !== ''" @click="quit">退出</li>
						<li v-if="username === ''" @click="regClick">注册</li>
						<li class="nav-pile">|</li>
						<li @click="aboutClick">关于</li>
					</ul>
				</div>
			</div>
		</div>
		<div class="app-content">
            <keep-alive>
            	<router-view></router-view>
            </keep-alive>
		</div>
		<div class="app-footer">
			<p>@ 2019 fishenal MIT</p>
		</div>
		<my-dialog :isShow="isShowLogDialog" @on-close="closeDialog('isShowLogDialog')">
			<log-form @has-log="onSuccessLog"></log-form>
		</my-dialog>
		<my-dialog :isShow="isShowRegDialog" @on-close="closeDialog('isShowRegDialog')">
			<reg-form></reg-form>
		</my-dialog>
		<my-dialog :isShow="isShowAboutDialog" @on-close="closeDialog('isShowAboutDialog')">
			<p>FBI WARNING</p>
			<p>本网站立足于美利坚合众国</p>
		</my-dialog>
	</div>
</template>

<script>
	import myDialog from './dialog'
	import logForm from './logform'
	import regForm from './regForm'
	export default {
		components: {
			myDialog,
			logForm,
			regForm
		},
		data() {
			return {
				isShowAboutDialog: false,
				isShowLogDialog: false,
				isShowRegDialog: false,
				username: ''
			}
		},
		methods: {
			logClick(){
				this.isShowLogDialog = true
			},
			regClick(){
				this.isShowRegDialog = true
			},
			aboutClick(){
				this.isShowAboutDialog = true
			},
			//将变量名传进来
			closeDialog(attr){
				this[attr] = false
			},
			//将子组件传出来的数据进行绑定
			onSuccessLog(data){
				this.closeDialog('isShowLogDialog')
				this.username = data.name
			}
		}
	}
</script>

<style>
	/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
/*作为根节点的layout用于放置所有的初始样式*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
.app-header {
	background-color: #363636;
	color: #b2b2b2;
	height: 90px;
	line-height: 90px;
	width: 100%;
}
.app-header-inner {
	width: 1200px;
	margin: 0 auto;
}
.header-logo {
	float: left;
}
.app-header-inner img {
	width: 50px;
	margin-top: 20px;
}
.header-nav {
	float: right;
}
.header-nav ul {
	overflow: hidden;/*？？？？？*/
}
.header-nav li {
	cursor: pointer;/*？？？？？*/
	float: left;
}
.nav-pile {
	padding: 0 10px;
}
.app-footer {
	text-align: center;
	height: 80px;
	width: 100%;
	line-height: 80px;
	background-color: #e3e4e8;
	clear: both;/*??????*/
	margin-top: 30px;
}
.container {
	width: 1200px;
	margin: 0 auto;
}
.hr {
	height: 1px;
	width: 100%;
	background-color: #ddd;
}
.button {
	background-color: #4fc08d;
	color: #fff;
	display: inline-block;/*??????*/
	padding: 10px 20px;
	cursor: pointer;
}
.button:hover {
	background: #4fc08d;
}
.g-form {

}
.g-form-line {
  padding: 15px 0;
}
.g-form-label {
  width: 100px;
  font-size: 16px;
  display: inline-block;
}
.g-form-input {
  display: inline-block;
}
.g-form-input input {
  height: 30px;
  width: 200px;
  line-height: 30px;
  vertical-align: middle;
  padding: 0 10px;
  border: 1px solid #ccc;
}
.g-form-btn {
  padding-left: 100px;
}
.g-form-error {
  color: red;
  padding-left: 15px;
}
</style>


